<template>
  <div class="hello">
  <v-app light>
  <v-layout row wrap>
    <v-toolbar dark color="indigo" class="hidden-xs-only">
       <a href="https://twitter.com/el_gordo_">
       <v-avatar
          size="60"
          color="grey lighten-4">
          <img src="https://pbs.twimg.com/profile_images/668704533657309184/haZfgRQ8_400x400.png" alt="avatar">
        </v-avatar>
        </a>
        <v-toolbar-title class="hidden-sm-only">eCommerce <strong> Worldwide </strong> news by Carlos</v-toolbar-title>
        <a href="https://twitter.com/el_gordo_">
       <v-avatar
          size="40"
          class = "ma-2 hidden-sm-only">
          <img src="../assets/twitter.png" alt="twitter">
        </v-avatar>
        </a>
        <v-spacer></v-spacer>
        <v-toolbar-items>
          <v-btn flat
            v-for="item in items"
            :key="item.title"
            router
            :to="item.link">
            {{item.title}}
          </v-btn>
               
        </v-toolbar-items>
    </v-toolbar>
    <v-toolbar dark color="indigo" class="hidden-sm-and-up">
       <v-flex d-flex xs2>
        <a href="https://twitter.com/el_gordo_">
          <v-avatar
          size="60">
          <img src="https://pbs.twimg.com/profile_images/668704533657309184/haZfgRQ8_400x400.png" alt="avatar">
        </v-avatar>
        </a>
       </v-flex>
        <v-flex d-flex xs10>
          <v-toolbar-items class="mx-0">
         
          <v-btn flat
          v-for="item in items"
          :key="item.title"
          router
          :to="item.link"
          class="mx-0 px-0"
          >
          
             <v-img height=35px :src="item.imgsrc" contain></v-img>
         
          </v-btn>
        
          </v-toolbar-items>
        </v-flex> 
    </v-toolbar>

   </v-layout>
  <main>
    <v-content>
      <v-container grid-list-lg >
        <v-layout row wrap>
          <v-flex d-flex xs12 sm6 md4 lg3 
            v-for="post in posts" :key="post.link">
              <v-card>

                <v-layout column d-flex fill-height align-content-space-between>
                 <a :href="post.link">
                 <v-img height="300px" class="amber--text mx-2" :src="post.imgUrl"></v-img>
                 </a>
                 <v-card-title primary-title>
                 <h3 class="headline ma-0">{{post.title}}</h3>
                </v-card-title>
                <v-card-text v-html="post.description"></v-card-text>
                <v-card-actions>
                  <h3 class="headline font-weight-light indigo--text ma-2">{{post.displayDate}}</h3>
                  <v-spacer></v-spacer>
                  <v-btn :href="post.link" color="primary">Read More</v-btn>
                </v-card-actions>

                </v-layout>
              </v-card>
            
          </v-flex>
        </v-layout>
       </v-container>
    </v-content>
  </main>
  <footer>
    <v-layout row wrap>
      <v-toolbar dark color="indigo">
          <a href="https://twitter.com/el_gordo_">
            <v-avatar
              size="60"
              color="grey lighten-4"
              >
              <img src="https://pbs.twimg.com/profile_images/668704533657309184/haZfgRQ8_400x400.png" alt="avatar">
            </v-avatar>
           </a>
          <v-toolbar-title> Suivez moi sur Twitter </v-toolbar-title>
        <a href="https://twitter.com/el_gordo_">
        <v-avatar
          size="40"
          class = "ma-2">
          <img src="../assets/twitter.png" alt="twitter">
        </v-avatar>
        </a>
          
      </v-toolbar>
    </v-layout>  
  </footer>
  </v-app>
  </div>
</template>

<script>
import db from './firebaseInit';
import moment from 'moment';
moment.locale('fr');
export default {
  name: 'ecomfrench',
  data () {
    return {
      items: [
          { title: 'Franais', imgsrc:"http://icons.iconarchive.com/icons/custom-icon-design/all-country-flag/48/France-Flag-icon.png", link:"/ecomfrench"},
          { title: 'English',  imgsrc:"http://icons.iconarchive.com/icons/custom-icon-design/flag-3/48/United-Kingdom-flag-icon.png", link:"/"},
          { title: 'Espaol',  imgsrc:"http://icons.iconarchive.com/icons/wikipedia/flags/48/ES-Spain-Flag-icon.png", link:"/ecomspan"},
          { title: 'Italiano',  imgsrc:"http://icons.iconarchive.com/icons/custom-icon-design/all-country-flag/64/Italy-Flag-icon.png", link:"/ecomitaliano"},
          { title: 'Deutsch',  imgsrc:"http://icons.iconarchive.com/icons/custom-icon-design/all-country-flag/64/Germany-Flag-icon.png", link:"/ecomgerman"}
        ],
        right: null,
      posts: []
    }
  },
  created() {
            db.collection('postEcommerceFrench')
            .orderBy("dateUnix", "desc")
            .limit(80)
            .get()
            .then((querySnapshot) => {
                querySnapshot.forEach((doc) =>
                { var initDate = doc.data().pubDate;
                  moment.locale('fr');
                  var dspDate = moment(initDate).format('MMMM DD');
                  var imgUrl = doc.data().imgUrl;
                  if (imgUrl == "") 
                    { const num = Math.floor((Math.random() * 10) + 1);
                     imgUrl = require('../assets/ecom'+num+'.jpeg')
                     };
                const data = {
                    'displayDate': dspDate,
                    'title': doc.data().title,
                    'description': doc.data().description,
                    'imgUrl': imgUrl,
                    'pubDate': doc.data().pubDate,
                    'link': doc.data().link
                }
                this.posts.push(data);
                
                })

                })
                
            },
}
</script>






