<template>
 <v-layout row wrap>
    <v-toolbar dark color="indigo" class="hidden-xs-only">
       <v-avatar
          size="60"
          color="grey lighten-4">
          <img src="https://pbs.twimg.com/profile_images/668704533657309184/haZfgRQ8_400x400.png" alt="avatar">
        </v-avatar>
        <v-toolbar-title class="hidden-sm-only">eCommerce <strong> Worldwide </strong> news by Carlos</v-toolbar-title>
        <v-spacer></v-spacer>
        <v-toolbar-items>
          <v-btn flat>
             <router-link to="/" tag="v-toolbar-items">English</router-link>
          </v-btn>
          <v-btn flat>
             <router-link to="/ecomspan" tag="v-toolbar-items">Espaol</router-link>
          </v-btn>
          <v-btn flat>
            <router-link to="/ecomfrench" tag="v-toolbar-items"> Franais</router-link>
          </v-btn>
        </v-toolbar-items>
   </v-toolbar>

   <v-toolbar dark color="indigo" class="hidden-sm-and-up">
       <v-avatar
          size="60"
          color="grey lighten-4">
          <img src="https://pbs.twimg.com/profile_images/668704533657309184/haZfgRQ8_400x400.png" alt="avatar">
        </v-avatar>
        <v-spacer></v-spacer>
        <v-toolbar-items class="mx-1">
          <v-btn flat
          v-for="item in items"
          :key="item.title"
          router
          :to="item.link"
          >
             <v-img height=35px :src="item.imgsrc" contain></v-img>
          </v-btn>
        </v-toolbar-items>
   </v-toolbar>

   </v-layout>
</template>

<script>
  export default {
    name:'navigationsm',
    data () {
      return {
        items: [
          { title: 'French', imgsrc:"http://icons.iconarchive.com/icons/custom-icon-design/all-country-flag/48/France-Flag-icon.png", link:"/ecomfrench"},
          { title: 'English',  imgsrc:"http://icons.iconarchive.com/icons/custom-icon-design/flag-3/48/United-Kingdom-flag-icon.png", link:"/"},
          { title: 'Spanish',  imgsrc:"http://icons.iconarchive.com/icons/wikipedia/flags/48/ES-Spain-Flag-icon.png", link:"/ecomspan"}
        ],
        right: null
      }
    }
  }
</script>






