<template>
  <v-navigation-drawer
    :mini-variant.sync="mini"
    hide-overlay
    v-model="drawer"
    
    stateless
  >
    <v-toolbar flat class="transparent">
      <v-list class="pa-0">
        <v-list-tile avatar>
          <v-list-tile-avatar>
            <img src="https://pbs.twimg.com/profile_images/668704533657309184/haZfgRQ8_400x400.png">
          </v-list-tile-avatar>

          <v-list-tile-content>
            <v-list-tile-title>John Leider</v-list-tile-title>
          </v-list-tile-content>

          <v-list-tile-action>
            <v-btn
              icon
              @click.stop="mini = !mini"
            >
              <v-icon>chevron_left</v-icon>
            </v-btn>
          </v-list-tile-action>
        </v-list-tile>
      </v-list>
    </v-toolbar>

    <v-list >
      <v-divider></v-divider>

      <v-list-tile
        v-for="item in items"
        :key="item.title"
        
      >
        <v-list-tile-action>
          <v-img height=40px class="ma-0" :src="item.imgsrc"></v-img>
        </v-list-tile-action>

        <v-list-tile-content>
          <v-list-tile-title>{{ item.title }}</v-list-tile-title>
        </v-list-tile-content>
      </v-list-tile>
    </v-list>
  </v-navigation-drawer>
</template>

<script>

import db from './firebaseInit';
export default {
  name: 'navigation',
     data () 
    {
      return {
        drawer: true,
        items: [
          { title: 'French', imgsrc:"http://icons.iconarchive.com/icons/custom-icon-design/all-country-flag/48/France-Flag-icon.png"},
          { title: 'English', imgsrc:"http://icons.iconarchive.com/icons/custom-icon-design/flag-3/48/United-Kingdom-flag-icon.png"}
        ],
        mini: true,
        right: null
      }
    },
    created() 
        {
            db.collection('postEcommerce')
            .orderBy("dateUnix", "asc")
            .limit(1)
            .get()
            .then((querySnapshot) => 
              {querySnapshot.forEach((doc) =>
                { console.log(JSON.stringify(doc.data())) });
                db.collection('postEcommerce').doc(doc.id).delete()
                .then(Ref => { console.log("duplicated post removed" + JSON.stringify(Ref)) })
              })

            
                
        }

}
</script>






