import Vue from 'vue'
import Router from 'vue-router'
import ecomm from '@/components/ecomm'
import ecomspanish from '@/components/ecomspanish'
import navigation from '@/components/navigation'
import navigationsm from '@/components/navigationsm'
import ecomfrench from '@/components/ecomfrench'
import ecomitaliano from '@/components/ecomitaliano'
import ecomgerman from '@/components/ecomgerman'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'ecomm',
      component: ecomm
    },
    {
      path: '/ecomspan',
      name: 'ecomspanish',
      component: ecomspanish
    },
    {
      path: '/navigation',
      name: 'navigation',
      component: navigation
    },
    {
      path: '/navigationsm',
      name: 'navigationsm',
      component: navigationsm
    },
    {
      path: '/ecomfrench',
      name: 'ecomfrench',
      component: ecomfrench
    },
    {
      path: '/ecomitaliano',
      name: 'ecomitaliano',
      component: ecomitaliano
    },
    {
      path: '/ecomgerman',
      name: 'ecomgerman',
      component: ecomgerman
    }
    
  ]
})



